<template>
  <teleport to="#modal">
    <div class="modal d-block" tabindex="-1" v-if="visible">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{title}}</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click.prevent="onClost"></button>
          </div>
          <div class="modal-body">
            <slot></slot>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click.prevent="onClost">{{butText.closeText}}</button>
            <button type="button" class="btn btn-primary" @click.prevent="onConfirm">{{butText.confirmText}}</button>
          </div>
        </div>
      </div>
    </div>
  </teleport>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { useCreateNode } from '../hooks/usecreateNode'
export default defineComponent({
  name: 'Modal',
  props: {
    title: String,
    visible: {
      type: Boolean,
      default: false
    },
    butText: Object
  },
  emits: ['modal-on-confirm', 'modal-on-close'],
  setup (props, context) {
    useCreateNode('modal')
    const onClost = () => {
      context.emit('modal-on-close')
    }
    const onConfirm = () => {
      context.emit('modal-on-confirm')
    }

    return { onClost, onConfirm }
  }
})
</script>
