<template>
  <div class="home-page">
    <section class="py-5 text-center container">
      <div class="row py-lg-5">
        <div class="col-lg-6 col-md-8 mx-auto">
          <img src="../assets/callout.svg" alt="callout" class="w-50"/>
          <h2 class="font-weight-light">随心写作，自由表达</h2>
          <p>
            <a href="#" class="btn btn-primary my-2">开始写文章</a>
          </p>
        </div>
      </div>
    </section>
    <h4 class="font-weight-bold text-center">发现精彩</h4>
    <column-list :list="list"></column-list>
    <div class="w-100 d-flex justify-content-center">
      <button
      @click="loadMorePage"
      v-if="!isLastPage"
      class="btn btn-primary w-25 mx-auto mt-2 mb-5 btn-block"
      >点击更多</button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from 'vue'

import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import { useLoadMore } from '../hooks/useLoadMore'
import ColumnList from '../components/ColumnList.vue'
export default defineComponent({
  name: 'Home',
  components: {
    ColumnList
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const total = computed(() => store.state.columns.total)
    const currentPageVal = computed(() => store.state.columns.currentPage)
    const { loadMorePage, isLastPage } = useLoadMore('getColumns', total, { currentPage: currentPageVal.value + 1 || 2, pageSize: 3 })
    onMounted(() => {
      store.dispatch('getColumns', { currentPage: currentPageVal.value || 1, pageSize: 3 })
    })
    const list = computed(() => store.getters.getColumnList)
    return {
      list,
      isLastPage,
      loadMorePage
    }
  }
})
</script>
