
import { defineComponent, computed, onMounted } from 'vue'

import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import { useLoadMore } from '../hooks/useLoadMore'
import ColumnList from '../components/ColumnList.vue'
export default defineComponent({
  name: 'Home',
  components: {
    ColumnList
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const total = computed(() => store.state.columns.total)
    const currentPageVal = computed(() => store.state.columns.currentPage)
    const { loadMorePage, isLastPage } = useLoadMore('getColumns', total, { currentPage: currentPageVal.value + 1 || 2, pageSize: 3 })
    onMounted(() => {
      store.dispatch('getColumns', { currentPage: currentPageVal.value || 1, pageSize: 3 })
    })
    const list = computed(() => store.getters.getColumnList)
    return {
      list,
      isLastPage,
      loadMorePage
    }
  }
})
