<template>
  <div class="row">
    <div class="col-4 mb-4"  v-for="column in columnList" :key="column._id">
      <div class="card h-100 shadow-sm">
        <div class="card-body text-center">
          <img :src="column.avatar && column.avatar.fitUrl" class="rounded-circle border border-light my-3 w-25" :alt="column.title">
          <h5 class="card-title">{{column.title}}</h5>
          <p class="card-text text-left">{{column.description}}</p>
          <router-link :to="`/columnDetail/${column._id}`" class="btn btn-outline-primary">进入专栏</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, computed } from 'vue'
import { addColumnAvatar } from '../helps'
import { ColumnProps } from '../store'

// list方法封装
const columnListFunc = (props: any) :any => {
  const columnList = computed(() => {
    return props.list.map((column: any) => {
      addColumnAvatar(column, 50, 50)
      return column
    })
  })
  return { columnList }
}

export default defineComponent({
  name: 'ColumnList',
  props: {
    list: {
      type: Array as PropType<ColumnProps[]>,
      required: true
    }
  },
  setup (props) {
    const { columnList } = columnListFunc(props)

    return { columnList }
  }
})
</script>
